import "./BasketProduct.css"
import React from 'react'
import {BASKET_ACTIONS} from "./Home.js"
import {Link} from "react-router-dom"

function BasketProduct({id, name, price, description, image, size, era, notes, basketDispatch,setPage,setProductPageProduct}) {

    function goToProduct(){
        setProductPageProduct({
                            id:id,
                            name:name,
                            price:price, 
                            image:image, 
                            description:description,
                            size:size,
                            era:era,
                            notes:notes
        })
        setPage("productPage")
    }
    return (
        <div class="trunkProduct">
                    <Link to={`\${id}`}>
                        <img onClick={goToProduct} src={image} alt=""/>
                    </Link>
                    <div class="itemInfo">
                        <Link to={`\${id}`}>
                        <a><h3 onClick={goToProduct}>{name}</h3></a>
                        </Link>
                        <p>Size: {size}</p>
                        <p>Era: {era}</p>

                    </div>
                    <div class="priceAndRemove">
                        <h2>{price}</h2>
                        <button onClick={()=>{basketDispatch({type:BASKET_ACTIONS.REMOVE, payload:{id:id}})}} class="remove">
                            <img src="images/bin.png" alt=""/>
                        </button>
                    </div>
                    
        </div>
    )
}

export default BasketProduct


