import React,{useState,useEffect} from 'react';
import StripeCheckout from "react-stripe-checkout"
import axios from "axios";

import "./Basket.css";
import BasketProduct from "./BasketProduct.js";





function Basket({basketDispatch, basket, setPage, setProductPageProduct}) {
    const [total,setTotal]=useState(0);

    useEffect(()=>{
        let accumulator=0
        basket.map(basketProduct=>{
            accumulator+=basketProduct.price
        })
        setTotal(accumulator)
    },[basket])

    async function handleToken(token, addresses){
        const response=await axios.post("http://localhost:5000/checkout",{
            token,
            basket
        });
        const {status}=response.data;
        console.log(status); 
        if (status === 'success'){
            console.log("Success")
        }else{
            console.log("Failure")
        }
    }

    
    return (
        <div class="trunk">
            <h1>The Trunk:</h1>
            <div class="trunkList">
                {basket.map(basketProduct=>{
                    return <BasketProduct
                        id={basketProduct._id}
                        name={basketProduct.name}
                        price={basketProduct.price}
                        description={basketProduct.description}
                        image={basketProduct.image}
                        size={basketProduct.size}
                        era={basketProduct.era}
                        notes={basketProduct.notes}
                        basketDispatch={basketDispatch}
                        setPage={setPage}
                        setProductPageProduct={setProductPageProduct}
                    />
                })}
                
                <div class="total">
                    <h2>Total:</h2>
                    <h2>{total}</h2>
                    <StripeCheckout
                        stripeKey="pk_test_51ITv8TJjkYkHXubO8F7lh8kBuipteRA7SmC82VndZk9RyHXRBRXsCH1I2x8FBViLGtk9pFJtydXRUdl2tSVEWyf500Znuxp32q"
                        token={handleToken}
                        billingAddress
                        shippingAddress
                        amount={total*100}//*100 because converting to cents for the transaction
                        
                    />
                </div>
            </div>

        </div>    
           

      
    )
}

export default Basket;
