import React from 'react'
import Product from './Product'
import "./Listings.css"

function Listings({products, search, basketDispatch, setPage, getProductPage}) {
    let display=products.filter(product=>{
        let match=true
        for(let n=0;n<search.length;n++){
            if(product.name.toLowerCase().charAt(n)!==search.toLowerCase().charAt(n)){
                match=false;
            }
        }
        if(match){
            return product;
        }}).map(product =>{
        return(
        <Product
            key={product._id}
            product={product}
            basketDispatch={basketDispatch}
            setPage={setPage}
            getProductPage={getProductPage}
        />)
    })
    return (
        <div class="productListings">
            {display}
        </div>
    )
}

export default Listings
