import "./Home.css"

import React, {useState,useReducer,useEffect,useRef} from 'react'
import {BrowserRouter as Router, Route, Switch} from "react-router-dom"
import axios from 'axios'

import Navbar from "./Navbar"
import Basket from "./Basket"
import ProductPage from "./ProductPage"
import Listings from "./Listings"

export const PRODUCT_ACTIONS={
    DELETE:'delete',
    GET:'get',
    ADD_TO_BASKET:"add to basket"
}

export const BASKET_ACTIONS={
    REMOVE:'remove',
    ADD:"add"
}

function reducer1(basket,action){
    switch(action.type){
        case BASKET_ACTIONS.ADD:

            //Checks if one of one item is already in the basket
            for(let i=0;i<basket.length;i++){
                if(action.payload.product._id===basket[i]._id){
                    return [...basket]

                }
            }
            //If not, then add the item to the basket
            return [...basket,action.payload.product]
        case BASKET_ACTIONS.REMOVE:
            return basket.filter(basketProduct=>basketProduct._id !== action.payload.id)
        default:
            return basket
    }
}



function Home() {

    const [products, setProducts]=useState([]);
    const [basket,basketDispatch]=useReducer(reducer1,[]);
    const [page, setPage]=useState("");
    const [productPageProduct, setProductPageProduct]=useState({})
    const [search, setSearch]=useState("")
    const navRef=useRef();


    useEffect(()=>{
        if(page!==""){
            navRef.current.scrollIntoView()   
        } 
    },[page])


    useEffect(()=>{
        axios.get('http://localhost:5000/')
            .then(response=>{
                if(response.data.length>0){
                    console.log(response.data.length);
                    setProducts(response.data)
                    console.log(products)
                    
                }
            })
    },[])

    function getProductPage(id){
        axios.get('http://localhost:5000/'+id)
            .then(response=> setProductPageProduct(response.data))
    }

    
    return (
        <div ref={navRef} class="home">
            <Router>
            <Navbar
                basketAmount={basket.length}
                setPage={setPage}
                page={page}
                search={search}
                setSearch={setSearch}
            />

            
            <div class="listings">

                <Switch>
                    <Route exact path="/"> 
                        <Listings
                            products={products}
                            search={search}
                            basketDispatch={basketDispatch}
                            setPage={setPage}
                            getProductPage={getProductPage}
                        />
                    </Route>

                    <Route path="/Basket"> 
                        <Basket
                            basket={basket}
                            basketDispatch={basketDispatch}
                            setPage={setPage}
                            setProductPageProduct={setProductPageProduct}
                            getProductPage={getProductPage}
                        /> 
                    </Route>
                    <Route path="/:id"> 
                        <ProductPage
                            id={productPageProduct._id}
                            name={productPageProduct.name}
                            price={productPageProduct.price}
                            image={productPageProduct.image}
                            description={productPageProduct.description}
                            size={productPageProduct.size}
                            era={productPageProduct.era}
                            notes={productPageProduct.notes}
                            basketDispatch={basketDispatch}
                        />
                    </Route>
                </Switch>

                
                
            </div>
            </Router>


            <nav id="aboutNav" class="aboutNav navbar">
                <a href="#navbar" class="navbarItem navbarLogo"><div>
                    <h2>WAY  {"<"}</h2>
                    <h2>machine</h2>
                </div></a>


                <ul class="navbarItem">
                    <li><a class="aboutLink" href="#aboutNav">about</a></li>
                    <li><a class="contactLink" href="#contactNav">contact</a></li>
                </ul>

                





                <div class="navbarItem deloreanCartDiv">
                    <img class="deloreanCart"src="images/deloreanReal-removebg-preview (1).png" alt=""/>
                </div>

            </nav>

            <div id="about" class="about">
                <h1 class="aboutTitle"> THE WAY BACK{"<"}  machine</h1>

                <p class="aboutContent">
                    Traveling through time and space, the way back machine stumbles upon the rarest finds. Our
                    trunk is always packed and ready to be ransacked. We have great fashion finds
                    straight out of your mom's highschool closet, and 
                    gift ideas from places only accessible through interdimensional travel. Our
                    items are unique and sure to make you stand out. New items are added often so 
                    visit us whenever  you're feeling nostalgic and fashionable. 
                
                </p>
                <br/>
                <br/>
                <p class="aboutContent">
                    Need an item giftwrapped?! We're gift wrapping connoisseurs, 
                    providing gift experiences that will make gift recipients possibly
                    lose their minds (please advise those with weak hearts).
                </p>
            </div>

            <nav id="contactNav" class="aboutNav navbar">
                <a href="#navbar" class="navbarItem navbarLogo"><div>
                    <h2>WAY  {"<"}</h2>
                    <h2>machine</h2>
                </div></a>


                <ul class="navbarItem">
                    <li><a class="aboutLink" href="#aboutNav">about</a></li>
                    <li><a class="contactLink" href="#contactNav">contact</a></li>
                </ul>

                





                <div class="navbarItem deloreanCartDiv">
                    <img class="deloreanCart"src="images/deloreanReal-removebg-preview (1).png" alt=""/>
                </div>

            </nav>



            <div class="contact">
                <h1 class="contactTitle"> THE WAY BACK{"<"}  machine</h1>

                <form class="contactForm">
                    <div class="name">
                        <input class="fname contactInput" name="fname" value="First" type="text"/>
                        <input class="lname contactInput" name="lname" value="Last" type="text"/>
                    </div>
                    <input class="email contactInput" name="email" value="Email" />
                    <input class="subject contactInput" name="subject" value="Subject"type="text"/>
                    <textarea class="message contactInput" name="message" rows="10" cols="25"/>
                    <button class="submitButton" type="submit">Send Message</button>
                </form>

            </div>

            

        </div>
    )
}

export default Home
