import React,{useRef,useEffect} from 'react'
import {Link} from 'react-router-dom'




function Navbar(props) {
    
    

    return (
        <nav id="navbar" class="navbar">
            
            <Link to="/">
                    <div onClick={()=>{props.setPage("/")}} class="navbarItem navbarLogo">
                        <h2>WAY  {"<"}</h2>
                        <h2>machine</h2>
                    </div> 
            </Link>
               

            <form action="" class="navbarSearch">
                <input value={props.search} onChange={e => props.setSearch(e.target.value)} class="searchBar" type="text"/>
            </form>

            <ul class="navbarItem">
                <li><a class="aboutLink" href="#aboutNav">about</a></li>
                <li><a class="contactLink" href="#contactNav">contact</a></li>
            </ul>

            <div class="navbarItem deloreanCartDiv">
                <Link to={props.page==="basket"?"/":"/basket"}>
                    <img class="deloreanCart" onClick={()=>{props.page==="basket"?props.setPage("products"):props.setPage("basket")}} 
                    src={props.page==="basket"?"images/DeLorean-DMC-12-Open-removebg-preview.png":"images/deloreanReal-removebg-preview (1).png"} alt=""/>
                </Link>
                    
 
                <p class={props.page==="basket"?"openBasketAmount":"basketAmount"}>{props.basketAmount} </p> 
                
            </div>
                
        </nav>
    )
}

export default Navbar

