import React,{useRef} from 'react'
import {PRODUCT_ACTIONS} from "./Home.js"
import {BASKET_ACTIONS} from "./Home.js"
import {Link} from "react-router-dom"
import "./Product.css"


function Product({product, basketDispatch, setPage, getProductPage}) {

    function goToProduct(){
        getProductPage(product._id)
        setPage("productPage")
    }
    return (
        <div class="product">
            <img class="polaroid" src="images/poloroid-removebg-preview copy.png" alt=""/>
                <div class="productInfo">
                    <Link to={`/${product._id}`}>
                        <img onClick={goToProduct} class="productImage" src={product.image} alt=""/>
                    </Link>
                    <Link to={`/${product._id}`}>
                        <p onClick={goToProduct}>{product.name}</p>
                    </Link>
                    <h1>{product.price}</h1>
                    <img onClick={()=>basketDispatch({type:BASKET_ACTIONS.ADD, payload:{product:product}})} 
                    class="shoppingCart" src="images/shopping-cart.png" alt=""/>
                </div>
        </div>
    )
}

export default Product
