import React from 'react'
import {BASKET_ACTIONS} from "./Home"
import "./ProductPage.css"


function ProductPage({id, name, price, image, description, size, era, notes, basketDispatch}) {
    const product={_id:id,
        name:name,
        price:price,
        description:description,
        image:image,
        size:size,
        era:era,
        notes:notes}
    
        return (
        <div class="productPage">

            <div class="productPageProduct">
                <img src={product.image}/>
                <div class="productPageProductButtons">
                    <button><p>Buy<br/>Now!</p></button>
                    <button onClick={()=>basketDispatch({type:BASKET_ACTIONS.ADD, payload:{product:product}})}><img src="images/shopping-cart.png" alt=""/></button>
                </div>


            </div>
            <div class="productPageDescription">
                <div class="description">
                    <h2>{product.name}</h2>
                    <h2>{product.price}</h2>
                    <p>{product.description}
                    </p>
                </div>
                <p class="size">Size: {product.size}</p>
                <p class="era">Era: {product.era}</p>
                <p class="notes">Notes: {product.notes}</p>
               

            </div>
        </div>
    )
}

export default ProductPage
